@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face{font-family:montserrat}@font-face{font-family:'bebas neue'}*{font-family:montserrat}label,button{font-size:14px}text{font-size:12px}h1,h2,h3,h4,h5,h6{font-family:montserrat, 'bebas neue'}header{background-color:transparent;display:flex;flex-direction:column;font-size:18px;color:#000;padding:10px 0;display:flex;flex-direction:row;position:fixed;top:0;width:100vw;-webkit-transition:all .3s;transition:all .3s;z-index:100}header img{opacity:0;visibility:hidden;width:auto;height:60px;margin-left:30px}header div{margin-left:auto;margin-right:30px}header div ul{list-style-type:none;display:flex}header div ul li{margin-left:20px;cursor:pointer}header div ul li:first-of-type{margin-left:0px}header.scrolled{background-color:#282c34;color:#fff}header.scrolled img{-webkit-filter:invert(1);filter:invert(1)}header.scrolled img.show{opacity:1;visibility:visible}.container .banner{min-height:100vh;box-sizing:border-box;box-sizing:border-box;-moz-box-sizing:border-box}.container .banner{background-image:url(/thorikos/static/media/banner.2bdaffa1.png);background-position:center;background-size:cover;background-repeat:no-repeat;display:flex;position:relative}.container .banner img.banner-logo{width:auto;height:200px;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);-webkit-filter:invert(1);filter:invert(1);-webkit-transition:all .3s;transition:all .3s;z-index:101}.container .banner img.banner-logo.switch{height:60px;top:5%;left:83px;position:fixed}.container .banner img.banner-logo.hide{opacity:0;visibility:hidden}.container .introduction{display:flex;flex-direction:column;position:relative;min-height:100vh}.container .introduction .circle{position:absolute;width:200px;bottom:0}.container .introduction .seperator{margin:0;width:100%;height:auto}.container .introduction .flex{display:flex;height:100%}.container .introduction .flex div:first-of-type{margin:5% 25% 0 10%}.container .introduction .flex div:last-of-type{margin:5% 10% 0 0}.container .introduction .flex div{width:50%}.container .introduction .flex div .spots{position:absolute;width:35%;z-index:2;left:850px}.container .introduction .flex div .character{position:absolute;bottom:0;width:250px;left:900px;z-index:3}.container .introduction .flex h2{color:#531036;font-family:'bebas neue';font-size:40px}.container .introduction.location .flex div:first-of-type{margin-right:100px;padding-right:50px;box-sizing:border-box}.container .introduction.location .flex div .spots{z-index:-1;-webkit-transform:scale(-1, 1) rotate(-10deg);transform:scale(-1, 1) rotate(-10deg);left:750px}.container .introduction.location .flex div .location-img{width:350px;margin-top:225px}.container .introduction.location .circle{right:0;bottom:0;-webkit-transform:scale(-1, 1);transform:scale(-1, 1)}.container .introduction.about .circle{left:0;top:0}.container .introduction.background{background-image:url(/thorikos/static/media/background.2b1c736b.png);background-position:center;background-size:cover;background-repeat:no-repeat}.container .introduction.background .flex{box-sizing:border-box;margin-top:100px}.container .introduction.background .flex div:first-of-type{margin:5% 10% 0 0%}.container .introduction.background .flex div:last-of-type{padding-right:10%;box-sizing:border-box}.container .introduction.background .flex div h2{color:#fff}.container .introduction.background .flex div p{color:#fff}.container .introduction.background .flex div .character{position:absolute;bottom:0;width:250px;left:300px;z-index:3}.container .svg{min-height:100vh;display:flex;flex-direction:row;position:relative}.container .svg.map::after{background-image:url(/thorikos/static/media/Distribution-Map.534a93e2.png);background-position:center;background-size:83vw;background-repeat:no-repeat;content:"";opacity:0.1;top:0;left:15vw;bottom:5vh;right:0;position:absolute;z-index:-1}.container .svg .sidebar{background-color:#020304;color:#ffffff;width:15%;display:flex;flex-direction:column;box-shadow:0 0px 4px rgba(0,0,0,0.36),0 0px 9px rgba(0,0,0,0.43)}.container .svg .sidebar h1{width:80%;margin-left:auto;margin-right:auto}.container .svg .sidebar .timeperiods{margin:0 auto;width:80%}.container .svg .sidebar .timeperiods .filter{display:flex}.container .svg .sidebar .timeperiods .filter label{display:flex;width:100%}.container .svg .sidebar .timeperiods .filter label sup{font-size:10px;margin-left:auto;margin-right:5px}.container .svg .sidebar .timeperiods .filter input{margin-left:auto;margin-right:0}.container .svg .sidebar .filterBtn{width:80%;margin:10px auto;padding:5px;box-sizing:border-box;border-radius:3px;cursor:pointer;-webkit-transition:all .3s;transition:all .3s;outline:none}.container .svg .sidebar .filterBtn:hover{letter-spacing:1.2px}.container .svg svg{margin-top:auto;margin-left:10px;position:relative}.container .svg .tooltip{position:absolute;background-color:rgba(255,255,255,0.95);padding:0px 20px;border-radius:8px;border:1px solid rgba(0,0,0,0.36);-webkit-transition:opacity .3s;transition:opacity .3s;pointer-events:none}

